/* src/App.css */
body {
  font-family: Arial, sans-serif;
}

form {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

input, textarea {
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

button {
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
}

div {
  margin-bottom: 20px;
}

@media (max-width: 600px) {
  form {
    width: 100%;
  }

  input, textarea, button {
    width: 100%;
  }
}
